*, :before, :after {
  box-sizing: border-box;
  font-family: Gotham Rounded, sans-serif;
  font-weight: normal;
}

body {
  height: 100vh;
  background: linear-gradient(to right, #810383, #7b4b8776);
}

#Topics {
  float: center;
  text-align: center;
  width: 60%;
  color: #fff;
  text-shadow: .8px .8px .8px #ca40d4;
  background-color: #a566b3;
  border: 3px solid #7519a0;
  border-radius: 20px;
  margin: auto;
  padding: 10px;
  box-shadow: 4.4px 4.4px 4.4px #d341f0;
}

#showTopic {
  padding: 2rem;
}

